    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.node {
  position: relative;
  width: 1.2vw;
  height: 100%;
  margin: -2px auto;
  outline: 1px solid rgb(218, 225, 230);
  display: inline-block;
  box-sizing: border-box;
  background-color:#ECECEC
}

.node-finish {
  
  background-image: url(data:image/png;base64,);
  width: 1.2vw;
  height: 100%;
  margin: -2px auto;
}

.node-start {
  
  background-image: url(data:image/png;base64,);
}

.node-visited {
  -webkit-animation-name: visitedAnimation;
          animation-name: visitedAnimation;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}



.node-wall {
  -webkit-animation-name: wallAnimation;
          animation-name: wallAnimation;
  -webkit-animation-duration: 0.7s;
          animation-duration: 0.7s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

.node-shortest-path {
  -webkit-animation-name: shortestPath;
          animation-name: shortestPath;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}


@-webkit-keyframes visitedAnimation {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    background-color: rgba(0, 0, 66, 0.75);
    border-radius: 50%;
  }

  50% {
    background-color: #FF1D58;
  }

  75% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: #F75990;
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: #00DDFF;
  }
}


@keyframes visitedAnimation {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    background-color: rgba(0, 0, 66, 0.75);
    border-radius: 50%;
  }

  50% {
    background-color: #FF1D58;
  }

  75% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: #F75990;
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: #00DDFF;
  }
}

@-webkit-keyframes shortestPath {
  0% {
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    background-color: rgb(255, 254, 106);
  }

  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgb(255, 254, 106);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgb(255, 254, 106);
  }
}

@keyframes shortestPath {
  0% {
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    background-color: rgb(255, 254, 106);
  }

  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgb(255, 254, 106);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgb(255, 254, 106);
  }
}

@-webkit-keyframes wallAnimation {
  0% {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
    background-color: #303C6C;
  }

  50% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    background-color: #303C6C;
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: #303C6C;
  }
}

@keyframes wallAnimation {
  0% {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
    background-color: #303C6C;
  }

  50% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    background-color: #303C6C;
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: #303C6C;
  }
}
